import React from 'react'
import {
  Card,
  CardGrid,
  Group,
  List,
  ModalPage,
  ModalPageHeader,
  ModalPageProps,
  PanelHeaderBack,
  Separator,
  SimpleCell,
} from '@vkontakte/vkui'

import { useAppDispatch, useAppSelector } from 'hooks/redux'
import {
  selectAvailableTableSizes,
  selectChangeableTableSize,
} from 'store/selectors/tableGenerateOptions'
import { setTableOptionsSize } from 'store/slices/tableGenerateOptions'
import { getSizeWithX } from 'table/generator'
import { Icon24DoneOutline } from '@vkontakte/icons'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'

import { isAddSeparator } from 'utils/list'
import { useModalClose } from 'hooks/modal'
import { SeparatorSimpleCell } from 'components/ui/SeparatorSimpleCell/SeparatorSimpleCell'

export const ModalPageTableOptionsSize: React.FC<ModalPageProps> = (
  modalPageProps
) => {
  const dispatch = useAppDispatch()

  const sizes = useAppSelector(selectAvailableTableSizes)
  const changeableSize = useAppSelector(selectChangeableTableSize)

  const onClickBack = useModalClose()

  const onClickSizeNumber = (_size: number) => (e: React.MouseEvent) => {
    dispatch(setTableOptionsSize(_size))
    onClickBack && onClickBack()
  }

  return (
    <ModalPage
      {...modalPageProps}
      header={
        <>
          <ModalPageHeader before={<PanelHeaderBack onClick={onClickBack} />}>
            Размер
          </ModalPageHeader>
          <Separator wide />
        </>
      }
    >
      <Group>
        <CardGrid size='l'>
          <Card>
            <List>
              {sizes.map((size, idx) => (
                <React.Fragment key={size}>
                  <SimpleCell
                    activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                    after={size === changeableSize && <Icon24DoneOutline />}
                    onClick={onClickSizeNumber(size)}
                  >
                    {getSizeWithX(size)}
                  </SimpleCell>
                  {isAddSeparator(sizes, idx) && <SeparatorSimpleCell />}
                </React.Fragment>
              ))}
            </List>
          </Card>
        </CardGrid>
      </Group>
    </ModalPage>
  )
}
