import { useRouter } from '@happysanta/router'
import { SimpleCell } from '@vkontakte/vkui'
import { IndicatorTitle } from 'components/ui/IndicatorTitle/IndicatorTitle'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { MODAL_PAGES } from 'router/modals'

export const CellTableOptionsStyleSelected: React.FC = () => {
  const router = useRouter()
  const onClickStyleSelected = () => {
    router.pushModal(MODAL_PAGES.TABLE_STYLE_SELECTED)
  }
  return (
    <SimpleCell
      activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
      expandable
      onClick={onClickStyleSelected}
      indicator={<IndicatorTitle>Выкл</IndicatorTitle>}
    >
      Выбранные элементы
      {/* Стиль выбранных */}
    </SimpleCell>
  )
}
