import React from 'react'
import { useRouter } from '@happysanta/router'
import {
  Avatar,
  Card,
  CardGrid,
  FormItem,
  Group,
  Header,
  ImageBase,
  ModalPage,
  ModalPageHeader,
  ModalPageProps,
  PanelHeaderBack,
  RangeSlider,
  Separator,
  SimpleCell,
  Slider,
  Switch,
} from '@vkontakte/vkui'
import { IndicatorTitle } from 'components/ui/IndicatorTitle/IndicatorTitle'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { Value as RangeSliderValue } from '@vkontakte/vkui/dist/components/RangeSlider/RangeSlider'

import { TouchEvent as VKTouchEvent } from '@vkontakte/vkui/dist/components/Touch/Touch'
import { getRandomColor } from 'utils/color'
import { getRandomNumber } from 'utils/number'
import { MODAL_PAGES } from 'router/modals'
import { Icon24DoneOutline, Icon28SunOutline } from '@vkontakte/icons'
import { SeparatorSimpleCell } from 'components/ui/SeparatorSimpleCell/SeparatorSimpleCell'

export const ModalPageTableOptionsColor: React.FC<ModalPageProps> = (
  modalPageProps
) => {
  const router = useRouter()

  const onClickBack = () => router.popPageIfModal()

  const [S, setS] = React.useState(50)
  const [L, setL] = React.useState(60)

  const [d, setD] = React.useState(Date.now())

  const onChangeS = (value: number, e: VKTouchEvent) => {
    setS(value)
    console.log('S', value)
  }
  const onChangeL = (value: number, e: VKTouchEvent) => {
    setL(value)
    console.log('L', value)
  }
  const update = () => setD(Date.now())

  /*   const s = getRandomNumber(rangeS[0], rangeS[1])
  const l = getRandomNumber(rangeL[0], rangeL[1]) */
  return (
    <ModalPage
      {...modalPageProps}
      header={
        <>
          <ModalPageHeader before={<PanelHeaderBack onClick={onClickBack} />}>
            Цветные элементы
          </ModalPageHeader>
          <Separator wide />
        </>
      }
    >
      <Group>
        <Group
          mode='plain'
          separator='hide'
          // header={<Header mode='secondary'>Вариант Шульте-Горбова</Header>}
        >
          <CardGrid size='l'>
            <Card>
              <SimpleCell
                activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                // after={<Icon24DoneOutline />}
                expandable
                indicator={<IndicatorTitle>Ячейки</IndicatorTitle>}
                onClick={() =>
                  router.pushModal(MODAL_PAGES.TABLE_VARIANT_RED_BLACK)
                }
              >
                Вариант Шульте-Горбова
              </SimpleCell>
            </Card>
          </CardGrid>
        </Group>

        <Group
          mode='plain'
          separator='hide'
        >
          <CardGrid size='l'>
            <Card>
              <SimpleCell
                activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                // expandable
                disabled
                after={<Switch />}
              >
                Цветные символы
              </SimpleCell>
              <SeparatorSimpleCell />
              <SimpleCell
                activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                // expandable
                // onClick={() =>
                //   router.pushModal(MODAL_PAGES.TABLE_COLORED_CELLS)
                // }
                disabled
                after={<Switch />}
              >
                Цветные ячейки
              </SimpleCell>
            </Card>
          </CardGrid>
        </Group>

        <Group
          mode='plain'
          separator='hide'
        >
          <CardGrid size='l'>
            <Card>
              <SimpleCell
                activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                disabled
                after={<Switch />}
              >
                Обновить после выбора
              </SimpleCell>
            </Card>
          </CardGrid>
        </Group>
      </Group>
    </ModalPage>
  )
}

