import React from 'react'
import {
  Card,
  CardGrid,
  Group,
  ModalPage,
  ModalPageHeader,
  ModalPageProps,
  PanelHeaderBack,
  Separator,
  SimpleCell,
  Switch,
} from '@vkontakte/vkui'
import { GroupTableOptionsTransforms } from './groups/GroupTableOptionsTransforms'
import { useModalClose } from 'hooks/modal'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'

export const ModalPageTableOptionsTransforms: React.FC<ModalPageProps> = (
  modalPageProps
) => {
  const onClickBack = useModalClose()

  return (
    <ModalPage
      {...modalPageProps}
      header={
        <>
          <ModalPageHeader before={<PanelHeaderBack onClick={onClickBack} />}>
            Трансформации
          </ModalPageHeader>
          <Separator wide />
        </>
      }
    >
      <Group>
        {/*  <Group
          mode='plain'
          separator='hide'
        >
          <CardGrid size='l'>
            <Card>
              <SimpleCell
                activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                disabled
                after={<Switch />}
              >
                Выключено
              </SimpleCell>
            </Card>
          </CardGrid>
        </Group> */}

        <GroupTableOptionsTransforms />

        {/*  <Group
          mode='plain'
          separator='hide'
        > */}
        <CardGrid size='l'>
          <Card>
            <SimpleCell
              activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
              disabled
              after={<Switch />}
            >
              Обновить после выбора
            </SimpleCell>
          </Card>
        </CardGrid>
        {/*       </Group> */}
      </Group>
    </ModalPage>
  )
}
