import React from 'react'
import { AdaptivityProvider, ConfigProvider, Platform, ViewWidth, WebviewType } from '@vkontakte/vkui'

import bridge, { VKBridgeSubscribeHandler } from '@vkontakte/vk-bridge'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { setClientConfig } from 'store/slices/configSlice'
import { selectConfigAppearanceValue } from 'store/selectors/config'

export const VKUIProvider: React.FC = ({ children }) => {
   const dispatch = useAppDispatch()
   const appearanceValue = useAppSelector(selectConfigAppearanceValue)

   const listener: VKBridgeSubscribeHandler = React.useCallback(
      (e) => {
         //TODO: remove
         if (e.detail.type.startsWith('webpack')) return

         switch (e.detail.type) {
            case 'VKWebAppUpdateConfig': {
               dispatch(setClientConfig(e.detail.data))
               break
            }
            default: {
            }
         }

         console.log(e)
      },
      [dispatch]
   )

   React.useEffect(() => {
      bridge.subscribe(listener)
      bridge.send('VKWebAppInit')

      return () => bridge.unsubscribe(listener)
   }, [listener])

   return (
      <ConfigProvider webviewType={WebviewType.VKAPPS} appearance={appearanceValue} platform={Platform.IOS}>
         <AdaptivityProvider>{children}</AdaptivityProvider>
      </ConfigProvider>
   )
}
