import React from 'react'
import { Separator } from '@vkontakte/vkui'

import { GroupCard } from 'components/ui/GroupCard/GroupCard'
import { CellSound } from './cells/CellSound'
import { CellVibration } from './cells/CellVibration'
import { CellAppAppearance } from './cells/CellAppAppearance'
import { CellAppNotifications } from './cells/CellAppNotifications'

export const GroupApplicationSettings: React.FC = () => {
   return (
      <GroupCard
         separator='hide'
         // header={<Header mode='secondary'>Приложение</Header>}
      >
         <CellSound />
         <Separator wide />
         <CellVibration />
         <Separator wide />
         <CellAppAppearance />
         <Separator wide />
         <CellAppNotifications />
      </GroupCard>
   )
}
