import React from 'react'
import { Card, CardGrid, Group, List, SimpleCell } from '@vkontakte/vkui'

import { useAppDispatch, useAppSelector } from 'hooks/redux'

import { isAddSeparator } from 'utils/list'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { Icon24DoneOutline } from '@vkontakte/icons'

import { setTableOptionsDirectionSequence } from 'store/slices/tableGenerateOptions'
import { selectChangeableTableDirectionSequence } from 'store/selectors/tableGenerateOptions'
import { TypeTableSequence } from 'types/table'
import {
  TABLE_DIRECTION_SEQUENCE,
  dataTableDirectionsSequence,
} from 'data/table'
import { SeparatorSimpleCell } from 'components/ui/SeparatorSimpleCell/SeparatorSimpleCell'
import { useModalClose } from 'hooks/modal'

export const GroupTableOptionsSequence: React.FC = () => {
  const dispatch = useAppDispatch()
  const onClose = useModalClose()
  const changeableDirectionSequence = useAppSelector(
    selectChangeableTableDirectionSequence
  )

  const onClickDirectionSequence = (value: TypeTableSequence) => () => {
    dispatch(setTableOptionsDirectionSequence(value))
    onClose && onClose()
  }

  return (
    <Group
      mode='plain'
      separator='hide'
    >
      <CardGrid size='l'>
        <Card>
          <List>
            {dataTableDirectionsSequence.map((directionSequence, idx) => (
              <React.Fragment key={directionSequence}>
                <SimpleCell
                  activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                  after={
                    changeableDirectionSequence === directionSequence && (
                      <Icon24DoneOutline />
                    )
                  }
                  onClick={onClickDirectionSequence(directionSequence)}
                >
                  {TABLE_DIRECTION_SEQUENCE[directionSequence]}
                </SimpleCell>
                {isAddSeparator(dataTableDirectionsSequence, idx) && (
                  <SeparatorSimpleCell />
                )}
              </React.Fragment>
            ))}
          </List>
        </Card>
      </CardGrid>
    </Group>
  )
}
