import { createAsyncThunk } from '@reduxjs/toolkit'
import { cloneDeep } from 'lodash'
import { RootState } from 'store'
import { defaultTableGenerateOptions } from 'store/slices/tableGenerateOptions'

export const getTableGenerateOptions = createAsyncThunk('tableGenerateOptions/get', async (_, thunkAPI) => {
   try {
      //
      return defaultTableGenerateOptions
   } catch (e) {
      return thunkAPI.rejectWithValue(e)
   }
})

export const applyTableGenerateOptions = createAsyncThunk('tableGenerateOptions/apply', async (_, thunkAPI) => {
   try {
      const changeableTableGenerateOptions = (thunkAPI.getState() as RootState).tableGenerateOptions
         .changeableTableOptions

      return cloneDeep(changeableTableGenerateOptions)
   } catch (e) {
      return thunkAPI.rejectWithValue(e)
   }
})
