import { RootState } from 'store'

export const selectTableOptionsIsShuffleCells = (state: RootState) =>
  state.tableOptions.isShuffleCellsAfterSelect

export const selectTableOptionsIsMarkedSelected = (state: RootState) =>
  state.tableOptions.visibilitySelectedElements

export const selectTableOptionsIsShowTimeTable = (state: RootState) =>
  state.tableOptions.isShowTimeTable
