import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { cloneDeep } from 'lodash'
import {
  ITableOptions,
  //  TypeTableSymbolsSearchMode
} from 'types/table'

interface ITableOptionsState extends ITableOptions {}

export const defaultTableOptions: ITableOptions = {
  isShuffleCellsAfterSelect: true,

  isChangeColorsAfterSelect: false,
  isEnableColoredCells: false,
  isEnableColoredSymbols: false,

  backgroundColorSL: {
    saturation: 100,
    lightness: 50,
  },
  symbolColorSL: {
    saturation: 100,
    lightness: 50,
  },

  isEnableTransforms: false,
  isChangeTransformsAfterSelect: false,
  transforms: {},

  visibilitySelectedElements: 'visible',

  isShowTimeTable: true,
}

const initialState: ITableOptionsState = {
  ...cloneDeep(defaultTableOptions),
}

const tableOptionsSlice = createSlice({
  name: 'tableOptionsSlice',
  initialState,
  reducers: {
    setTableOptionsIsShuffleCellsAfterSelect: (
      state,
      action: PayloadAction<boolean>
    ) => {
      state.isShuffleCellsAfterSelect = action.payload
    },
    setTableOptionsIsShowTableTime: (state, action: PayloadAction<boolean>) => {
      state.isShowTimeTable = action.payload
    },
    setTableOptionsIsMarkedSelected: (
      state,
      action: PayloadAction<boolean>
    ) => {},
    setTableOptionsSymbolsSearchMode: (
      state,
      action: any // PayloadAction<TypeTableSymbolsSearchMode>
    ) => {
      //@ts-ignore
      state.symbolsSearchMode = action.payload
    },
  },
})

export const tableOptionsReducer = tableOptionsSlice.reducer
export const {
  setTableOptionsIsShowTableTime,
  setTableOptionsIsMarkedSelected,
  setTableOptionsSymbolsSearchMode,

  // setTableOptionsIsUpdateColorsAfterSelect,
  setTableOptionsIsShuffleCellsAfterSelect,
} = tableOptionsSlice.actions
