import React from 'react'
import { useRouter } from '@happysanta/router'

import { Group, Panel, PanelHeaderClose, PanelProps } from '@vkontakte/vkui'
import { PanelHeaderSeparator } from 'components/ui/PanelHeaderSeparator/PanelHeaderSeparator'
import { GroupOptionsSettings } from './groups/settings/GroupOptionsSettings'
import { GroupOptionsAbout } from './groups/about/GroupOptionsAbout'

const rubber = (offset: number, dimension: number, resistanceRate: number) => {
   if (offset < 0) {
      return offset
   }
   const offsettedResistance = offset * resistanceRate
   return (offsettedResistance * dimension) / (offsettedResistance + dimension)
}

export const PanelOptions: React.FC<PanelProps> = (panelProps) => {
   const router = useRouter()

   const groupRef = React.useRef<HTMLDivElement>(null)
   const pageYRef = React.useRef(0)

   const onClickBack = () => {
      router.popPage()
   }

   const onPointerDown: React.PointerEventHandler<HTMLDivElement> = (e) => {
      pageYRef.current = e.pageY

      //@ts-ignore
      groupRef.current.style.transition = ''
   }
   const onPointerMove: React.PointerEventHandler<HTMLDivElement> = (e) => {
      let offset = e.pageY > pageYRef.current ? e.pageY : pageYRef.current
      let dimension = e.pageY > pageYRef.current ? e.pageY - pageYRef.current : pageYRef.current - e.pageY

      let px = rubber(offset, dimension, 0.35)
      if (e.pageY < pageYRef.current) {
         px -= px * 2
      }
      console.log(px, dimension)

      //@ts-ignore
      requestAnimationFrame(() => (groupRef.current.style.transform = `translateY(${px}px)`))
   }
   const onPointerUp: React.PointerEventHandler<HTMLDivElement> = (e) => {
      pageYRef.current = 0
      requestAnimationFrame(() => {
         //@ts-ignore
         groupRef.current.style.transform = ''
         //@ts-ignore
         groupRef.current.style.transition = 'transform .3s ease-out'
      })
   }
   return (
      <Panel
         {...panelProps}
         // style={{ touchAction: 'none' }}
         // onPointerDown={onPointerDown}
         // onPointerMove={onPointerMove}
         // onPointerUp={onPointerUp}
         // onPointerCancel={onPointerUp}
      >
         <PanelHeaderSeparator before={<PanelHeaderClose onClick={onClickBack} />}>Опции</PanelHeaderSeparator>

         <Group getRootRef={groupRef}>
            <GroupOptionsSettings />
            <GroupOptionsAbout />
         </Group>
      </Panel>
   )
}
