import React from 'react'
import { useAppSelector } from 'hooks/redux'

import { selectConfigAppearanceType } from 'store/selectors/config'
import { IndicatorTitle } from 'components/ui/IndicatorTitle/IndicatorTitle'
import { CONFIG_APPEARANCE } from 'data/config'

export const IndicatorAppearance: React.FC = () => {
   const appearanceType = useAppSelector(selectConfigAppearanceType)
   return <IndicatorTitle>{CONFIG_APPEARANCE[appearanceType]}</IndicatorTitle>
}
