import React from 'react'
import { Image, ImageProps, classNames } from '@vkontakte/vkui'

import './ImageIcon.css'

type TypeImageIconColor = 'red' | 'green' | 'blue' | 'violet' | 'orange' | 'purple'

interface ImageIconProps extends Pick<ImageProps, 'size' | 'borderRadius'> {
   color: TypeImageIconColor
}

/**
 * `children` - Иконка 22 fill=white
 */
export const ImageIcon: React.FC<ImageIconProps> = ({ size = 28, borderRadius, color, children }) => {
   return (
      <Image size={size} withBorder borderRadius='s' className={classNames('ImageIcon', `ImageIcon--clr-${color}`)}>
         {children}
      </Image>
   )
}
