import React from 'react'
import { Group, Panel, PanelHeaderBack, PanelProps, SimpleCell } from '@vkontakte/vkui'
import { useRouter } from '@happysanta/router'
import { PanelHeaderSeparator } from 'components/ui/PanelHeaderSeparator/PanelHeaderSeparator'
import { GroupCard } from 'components/ui/GroupCard/GroupCard'
import { Icon28DoneOutline } from '@vkontakte/icons'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'

export const PanelSettingsMode: React.FC<PanelProps> = (panelProps) => {
   const router = useRouter()

   const onClickBack = () => {
      router.popPage()
   }

   return (
      <Panel {...panelProps}>
         <PanelHeaderSeparator before={<PanelHeaderBack onClick={onClickBack} label={'Настройки'} />}>
            Режим
         </PanelHeaderSeparator>
         <Group>
            <GroupCard>
               <SimpleCell activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY} after={<Icon28DoneOutline />}>
                  Стандартный
               </SimpleCell>
            </GroupCard>
            <GroupCard>
               <SimpleCell activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}>Без нажатий по таблице</SimpleCell>
            </GroupCard>
         </Group>
      </Panel>
   )
}
