import React from 'react'
import { Switch } from '@vkontakte/vkui'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { selectConfigIsActiveSound } from 'store/selectors/config'
import { setSound } from 'store/slices/configSlice'

export const SwitchSound: React.FC = () => {
   const dispatch = useAppDispatch()
   const isActiveSound = useAppSelector(selectConfigIsActiveSound)

   const onChangeIsActiveSound: React.ChangeEventHandler<HTMLInputElement> = (e) => {
      dispatch(setSound(e.target.checked))
   }
   return <Switch checked={isActiveSound} onChange={onChangeIsActiveSound} />
}
