import React from 'react'
import { Switch } from '@vkontakte/vkui'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { selectConfigIsActiveVibration } from 'store/selectors/config'
import { setVibration } from 'store/slices/configSlice'

export const SwitchVibration: React.FC = () => {
   const dispatch = useAppDispatch()
   const isActiveVibration = useAppSelector(selectConfigIsActiveVibration)

   const onChangeIsActiveVibration: React.ChangeEventHandler<HTMLInputElement> = (e) => {
      dispatch(setVibration(e.target.checked))
   }
   return <Switch checked={isActiveVibration} onChange={onChangeIsActiveVibration} />
}
