import { PayloadAction, createSlice } from '@reduxjs/toolkit'
import { TypeModeTraining } from './activeTrainingSlice'

interface ITrainingOptions {
  mode: TypeModeTraining
  timer: number
}
const initialState: ITrainingOptions = {
  mode: 'table',
  timer: 3,
}

const trainingOptionsSlice = createSlice({
  name: 'trainingOptions',
  initialState,
  reducers: {
    setTrainingOptionsMode: (
      state,
      action: PayloadAction<TypeModeTraining>
    ) => {
      state.mode = action.payload
    },
    setTrainingOptionsTimer: (state, action: PayloadAction<number>) => {
      state.timer = action.payload
    },
  },
  extraReducers: (builder) => {},
})

export const trainingOptionsReducer = trainingOptionsSlice.reducer

export const { setTrainingOptionsMode, setTrainingOptionsTimer } =
  trainingOptionsSlice.actions
