import React from 'react'
import { Switch } from '@vkontakte/vkui'
import { useAppDispatch } from 'hooks/redux'

// import { setTableOptionsIsMarkedSelected } from 'store/slices/tableOptions'
// import { selectTableOptionsIsMarkedSelected } from 'store/selectors/tableOptions'

export const SwitchTableTrackingMouse: React.FC = () => {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const dispatch = useAppDispatch()

  const [isTrackingMouse, setIsTrackingMouse] = React.useState(false)
  //    useAppSelector(selectTableOptionsIsMarkedSelected)

  const onChangeMarkedSelected: React.ChangeEventHandler<HTMLInputElement> = (
    e
  ) => {
    setIsTrackingMouse(e.target.checked)
    //   dispatch(setTableOptionsIsMarkedSelected(e.target.checked))
  }

  return (
    <Switch
      checked={isTrackingMouse}
      onChange={onChangeMarkedSelected}
    />
  )
}
