import React from 'react'
import { useRouter } from '@happysanta/router'
import {
  Avatar,
  Card,
  CardGrid,
  FormItem,
  Group,
  Header,
  Image,
  ImageBase,
  ModalPage,
  ModalPageHeader,
  ModalPageProps,
  PanelHeaderBack,
  Separator,
  SimpleCell,
  Slider,
  Switch,
} from '@vkontakte/vkui'
import { IndicatorTitle } from 'components/ui/IndicatorTitle/IndicatorTitle'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'

import { TouchEvent as VKTouchEvent } from '@vkontakte/vkui/dist/components/Touch/Touch'
import { getRandomColor, hslToHex } from 'utils/color'

export const ModalPageTableOptionsColoredCells: React.FC<ModalPageProps> = (
  modalPageProps
) => {
  const router = useRouter()

  const onClickBack = () => router.popPageIfModal()

  const [S, setS] = React.useState(100)
  const [L, setL] = React.useState(50)

  const [d, setD] = React.useState(Date.now())

  const onChangeS = (value: number, e: VKTouchEvent) => {
    setS(value)
    console.log('S', value)
  }
  const onChangeL = (value: number, e: VKTouchEvent) => {
    setL(value)
    console.log('L', value)
  }

  const update = () => setD(Date.now())

  return (
    <ModalPage
      {...modalPageProps}
      header={
        <>
          <ModalPageHeader before={<PanelHeaderBack onClick={onClickBack} />}>
            Цветные ячейки
          </ModalPageHeader>
          <Separator wide />
        </>
      }
    >
      <Group>
        <Group
          mode='plain'
          separator='hide'
        >
          <CardGrid size='l'>
            <Card>
              <SimpleCell
                disabled
                after={<Switch />}
              >
                {'Выключено'}
              </SimpleCell>
            </Card>
          </CardGrid>
        </Group>
        <Group
          mode='plain'
          separator='hide'
        >
          <CardGrid size='l'>
            <Card>
              <div
                style={{
                  display: 'grid',
                  gridTemplateColumns: 'repeat(4,1fr)',
                  gridTemplateRows: 'repeat(3,1fr)',
                  gap: 8,
                  justifyContent: 'center',
                  alignItems: 'center',
                  justifyItems: 'center',
                  padding: '12px 0',
                }}
              >
                {Array.from({ length: 12 }, (_, idx) => (idx + 1) * 30).map(
                  (deg, idx) => (
                    <Image
                      size={48}
                      key={idx}
                      style={{
                        backgroundColor: '#' + hslToHex(deg, S, L),
                        color: 'white',
                      }}
                    >
                      {deg}
                    </Image>
                  )
                )}
                {/* <Image
                  size={48}
                  style={{
                    marginRight: 8,
                    backgroundColor: '#' + hslToHex(0, S, L),
                  }}
                >
                  R
                </Image>
                <Image
                  size={48}
                  style={{
                    marginRight: 8,
                    backgroundColor: '#' + hslToHex(120, S, L),
                  }}
                >
                  G
                </Image>
                <Image
                  size={48}
                  style={{ backgroundColor: '#' + hslToHex(240, S, L) }}
                >
                  B
                </Image> */}
              </div>
            </Card>
          </CardGrid>
        </Group>
        <Group
          mode='plain'
          separator='hide'
        >
          <Header mode='secondary'>Насыщенность</Header>
          <CardGrid size='l'>
            <Card>
              <FormItem
              //  top='Насыщенность'
              >
                <Slider
                  min={20}
                  max={100}
                  step={1}
                  value={S}
                  onChange={onChangeS}
                />
              </FormItem>
            </Card>
          </CardGrid>
          <Header mode='secondary'>Яркость</Header>
          <CardGrid size='l'>
            <Card>
              {/* <Separator wide /> */}
              <FormItem
              // top='Яркость'
              >
                <Slider
                  min={20}
                  max={80}
                  step={1}
                  value={L}
                  onChange={onChangeL}
                />
              </FormItem>
            </Card>
          </CardGrid>
        </Group>
      </Group>
    </ModalPage>
  )
}
