import { configureStore } from '@reduxjs/toolkit'
import { activeTableReducer } from './slices/activeTableSlice'
import { configReducer } from './slices/configSlice'
import { tableGenerateOptionsReducer } from './slices/tableGenerateOptions'
import { tableOptionsReducer } from './slices/tableOptions'
import { activeTrainingReducer } from './slices/activeTrainingSlice'
import { trainingOptionsReducer } from './slices/trainingOptionsSlice'

export const store = configureStore({
  reducer: {
    config: configReducer,
    tableOptions: tableOptionsReducer,
    tableGenerateOptions: tableGenerateOptionsReducer,
    activeTable: activeTableReducer,

    activeTraining: activeTrainingReducer,
    trainingOptions: trainingOptionsReducer,
  },
})

export type RootState = ReturnType<typeof store.getState>
export type AppDispatch = typeof store.dispatch
