import React from 'react'
import { IndicatorTitle } from 'components/ui/IndicatorTitle/IndicatorTitle'
import { useAppSelector } from 'hooks/redux'

import {
   selectChangeableTableDirectionSequence,
   selectIsDisabledChangeDirectionSequence,
} from 'store/selectors/tableGenerateOptions'
import { TABLE_DIRECTION_SEQUENCE } from 'data/table'
import { Icon28LockOutline } from '@vkontakte/icons'

export const IndicatorTableOptionsSequence: React.FC = () => {
   const directionSequence = useAppSelector(selectChangeableTableDirectionSequence)
   const isDisabledChangeDirectionSequence = useAppSelector(selectIsDisabledChangeDirectionSequence)
   const style = { display: 'flex', alignItems: 'center' }
   return (
      <IndicatorTitle style={isDisabledChangeDirectionSequence ? style : undefined}>
         {isDisabledChangeDirectionSequence && <Icon28LockOutline width={22} height={22} style={{ marginRight: 4 }} />}
         {TABLE_DIRECTION_SEQUENCE[directionSequence]}
      </IndicatorTitle>
   )
}
