import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import {
  applyTableGenerateOptions,
  getTableGenerateOptions,
} from 'store/asyncThunks/tableGenerateOptions'

import {
  ITableOptions,
  ITableTransform,
  ITableTransforms,
  // TypeTableSymbolsSearchMode,
  TypeTableSequence,
  TypeTableVariant,
  ITable,
  ITableGenerateOptions,
} from 'types/table'

import cloneDeep from 'lodash/cloneDeep'

import { defaultTable } from 'table/data'

import { MAX_TABLE_SIZE, MIN_TABLE_SIZE } from 'constants/constants'
import { generateTable } from 'table/generator'

export const getSizeWithX = (size: number) => `${size}x${size}`

export const _fillSizes = (max: number) => {
  const sizes: number[] = []
  for (let _size = MIN_TABLE_SIZE; _size <= max; _size++) {
    sizes.push(_size)
  }
  return sizes
}

export const defaultTableSize = 5
export const defaultTableDirectionSequence: TypeTableSequence = 'default'
export const defaultTableVariant: TypeTableVariant = 'standard'

export const defaultTableTransforms: ITableTransforms = {
  isTurn: false,
  isFlipX: false,
  isFlipY: false,
  // isSpin: false,
}
export const defaultTableGenerateOptions: ITableGenerateOptions = {
  variant: defaultTableVariant,
  size: defaultTableSize,

  directionSequence: defaultTableDirectionSequence,

  isColoredSymbols: true,
  // isUpdateColorsAfterSelect: true,
  // isAutoChangeColors: false,

  transforms: { ...defaultTableTransforms },
}

interface ITableGenerateOptionsState {
  currentTableOptions: ITableGenerateOptions
  changeableTableOptions: ITableGenerateOptions

  table: ITable

  // dataTableOptions: IDataTableOptions

  availableTableSizes: number[]
  isDisabledChangeDirectionSequence: boolean
  // isDisabledChangeShowCurrentSymbol: boolean
  // isDisabledChangeUpdateColorsAfterSelect: boolean
}

const initialState: ITableGenerateOptionsState = {
  currentTableOptions: cloneDeep(defaultTableGenerateOptions),
  changeableTableOptions: cloneDeep(defaultTableGenerateOptions),
  table: defaultTable,

  availableTableSizes: _fillSizes(MAX_TABLE_SIZE),
  isDisabledChangeDirectionSequence: false,
}

const tableGenerateOptionsSlice = createSlice({
  name: 'tableGenerateOptions',
  initialState,
  reducers: {
    setTableOptionsSize: (state, action: PayloadAction<number>) => {
      state.changeableTableOptions.size = action.payload

      const { d, s } = generateTable({
        size: action.payload,
        variant: 'standard',
        directionSequence: 'default',
        isColoredSymbols: false,
      })

      state.table.displayedCells = d
      state.table.sequenceCells = s
    },
    setMainTableOptionsVariant: (
      state,
      action: PayloadAction<TypeTableVariant>
    ) => {
      state.changeableTableOptions.variant = action.payload

      if (action.payload === 'red_black') {
        state.isDisabledChangeDirectionSequence = true

        state.changeableTableOptions.directionSequence =
          defaultTableDirectionSequence
      } else {
        state.isDisabledChangeDirectionSequence = false
      }
    },
    setTableOptionsDirectionSequence: (
      state,
      action: PayloadAction<TypeTableSequence>
    ) => {
      state.changeableTableOptions.directionSequence = action.payload

      if (action.payload === 'random') {
        // state.isDisabledChangeShowCurrentSymbol = true
        // state.changeableTableOptions.isShowCurrentSymbol = true
      } else {
        // state.isDisabledChangeShowCurrentSymbol = false
      }
    },

    setTableOptionsIsColoredSymbols: (
      state,
      action: PayloadAction<boolean>
    ) => {
      state.changeableTableOptions.isColoredSymbols = action.payload

      if (action.payload) {
        // state.isDisabledChangeUpdateColorsAfterSelect = false
      } else {
        // state.changeableTableOptions.isUpdateColorsAfterSelect = false
        // state.isDisabledChangeUpdateColorsAfterSelect = true
      }
    },

    // setTableOptionsIsUpdateColorsAfterSelect: (state, action: PayloadAction<boolean>) => {
    //    state.changeableTableOptions.isUpdateColorsAfterSelect = action.payload
    // },

    /* setTableTransformOption: (state, action: PayloadAction<{ name: string; checked: boolean }>) => {
         if (Object.keys(state.changeableTableOptions.transforms).some((v) => v === action.payload.name)) {
            state.changeableTableOptions.transforms[action.payload.name as TypeTableTransform] = action.payload.checked
         }
      }, */
  },
  extraReducers: (builder) => {
    builder.addCase(getTableGenerateOptions.pending, (state, action) => {})
    builder.addCase(getTableGenerateOptions.fulfilled, (state, action) => {
      state.currentTableOptions = action.payload
      state.changeableTableOptions = action.payload
    })
    builder.addCase(getTableGenerateOptions.rejected, (state, action) => {})

    //===
    builder.addCase(applyTableGenerateOptions.pending, (state, action) => {})
    builder.addCase(applyTableGenerateOptions.fulfilled, (state, action) => {
      state.currentTableOptions = action.payload
      state.changeableTableOptions = action.payload
    })
    builder.addCase(applyTableGenerateOptions.rejected, (state, action) => {})
  },
})

export const {
  setTableOptionsSize,
  setMainTableOptionsVariant,
  setTableOptionsDirectionSequence,

  setTableOptionsIsColoredSymbols,

  // setTableTransformOption,
} = tableGenerateOptionsSlice.actions

export const tableGenerateOptionsReducer = tableGenerateOptionsSlice.reducer
