import { RootState } from 'store'

//main
export const selectChangeableTableSize = (state: RootState) => state.tableGenerateOptions.changeableTableOptions.size

export const selectChangeableTableVariant = (state: RootState) =>
   state.tableGenerateOptions.changeableTableOptions.variant

export const selectChangeableTableTransforms = (state: RootState) =>
   state.tableGenerateOptions.changeableTableOptions.transforms

export const selectChangeableTableDirectionSequence = (state: RootState) =>
   state.tableGenerateOptions.changeableTableOptions.directionSequence

//colored
export const selectChangeableTableIsColoredSymbols = (state: RootState) =>
   state.tableGenerateOptions.changeableTableOptions.isColoredSymbols

export const selectAvailableTableSizes = (state: RootState) => state.tableGenerateOptions.availableTableSizes

export const selectIsDisabledChangeDirectionSequence = (state: RootState) =>
   state.tableGenerateOptions.isDisabledChangeDirectionSequence

export const selectDataTableTransforms = (state: RootState) => state.tableGenerateOptions
