const HeaderTime: React.FC = () => {
  const dispatch = useAppDispatch()
  // const isShowTableTime = useAppSelector(selectActiveTableIsShowTimeTable)
  const tableId = useAppSelector((state) => state.activeTable.data.id)

  React.useEffect(() => {
    dispatch(startSolution())
  }, [tableId])
  // if (!isShowTableTime) return null

  return <TableTime />
}

export const PanelTable: React.FC<PanelProps> = (panelProps) => {
  const router = useRouter()
  const tableSize = useAppSelector(selectActiveTableOptionsSize)
  const displayedCells = useAppSelector(selectActiveTableDisplayedCells)

  const onClickBack = () => {
    router.popPage()
  }
  return (
    <Panel
      {...panelProps}
      className='Panel--no-tabbar'
    >
      <PanelHeaderSeparator
        before={
          <>
            <PanelHeaderBack onClick={onClickBack} />
            <TableHeaderAside />
          </>
        }
      >
        <HeaderTime />
      </PanelHeaderSeparator>
      {/* <ProgressTable /> */}

      <Group className='Group--stretched Group--align-center'>
        <TableWrapper
          top={<TableHeader />}
          table={
            <TableView
              tableSize={tableSize}
              displayedCells={displayedCells}
            />
          }
        />
      </Group>

      {/* <Group className='Group--stretched'>
            <Group >
               <SimpleCell indicator={'1 м 15 с'}>Время</SimpleCell>
               <SimpleCell indicator={'1.67с'}>Среднее время поиска</SimpleCell>
               <SimpleCell indicator={'98%'}>Точноть</SimpleCell>
               <SimpleCell indicator={'3'}>Ошибки</SimpleCell>
            </Group>
            <TableOptions />
            <Group >
               <Div>
                  <ButtonGroup mode='vertical' stretched align='center' gap='m'>
                     <Button stretched size='l'>
                        Продолжить
                     </Button>
                     <Button stretched size='l' mode='tertiary'>
                        Главная
                     </Button>
                  </ButtonGroup>
               </Div>
            </Group>
         </Group> */}
    </Panel>
  )
}

const TableOptions: React.FC = () => {
  const [expand, setExpand] = React.useState(false)

  const toggle = () => {
    setExpand((prev) => !prev)
  }
  return (
    <Group
      separator='hide'
      header={
        <Header
          mode='secondary'
          aside={
            <Button
              activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
              size='m'
              before={expand ? <Icon16DropdownFlipped /> : <Icon16Dropdown />}
              mode='tertiary'
              onClick={toggle}
            >
              {expand ? 'Свернуть' : 'Развернуть'}
            </Button>
          }
        >
          Параметры таблицы
        </Header>
      }
    >
      {expand && <ListTableOptions />}
    </Group>
  )
}
const ListTableOptions: React.FC = () => {
  return (
    <List>
      <SimpleCell indicator={'variant'}>Вариант</SimpleCell>
      <SimpleCell indicator={'variant'}>Lorem ipsum dolor sit,</SimpleCell>
      <SimpleCell indicator={'variant'}>Lorem, ipsum dolor.</SimpleCell>
      <SimpleCell indicator={'variant'}>Вариант</SimpleCell>
      <SimpleCell indicator={'variant'}>Вариант</SimpleCell>
      <SimpleCell indicator={'variant'}>Вариант</SimpleCell>
      <SimpleCell indicator={'variant'}>Вариант</SimpleCell>
      <SimpleCell indicator={'variant'}>Вариант</SimpleCell>
    </List>
  )
}

export const ProgressTable = () => {
  const ref = React.useRef<HTMLDivElement>(null)

  const handleRef = React.useRef<number>()

  const [value, setValue] = React.useState(100)
  const startedAt = React.useRef(Date.now())

  const date = new Date()
  date.setSeconds(date.getSeconds() + 5)

  const completedAt = React.useRef(date.getTime())

  const updateValue = () => {
    const v =
      ((completedAt.current - Date.now()) /
        (completedAt.current - startedAt.current)) *
      100

    console.log(v)

    if (v <= 0) {
      setValue(0)
      if (handleRef.current !== undefined) {
        cancelAnimationFrame(handleRef.current)
      }

      // console.log(startedAt.current, completedAt.current, Date.now())

      return
    }
    if (ref.current) {
      //@ts-ignore
      ref.current.firstChild.style.width = `${v}%`
    }
    // setValue(v)

    handleRef.current = requestAnimationFrame(updateValue)
  }

  React.useEffect(() => {
    handleRef.current = requestAnimationFrame(updateValue)

    return () => {
      if (handleRef.current !== undefined) {
        cancelAnimationFrame(handleRef.current)
      }
    }
  }, [])

  return (
    <Progress
      getRootRef={ref}
      value={value}
    />
  )
}
