import React from 'react'
import { useRouter } from '@happysanta/router'
import { useAppSelector } from 'hooks/redux'
import { selectIsDisabledChangeDirectionSequence } from 'store/selectors/tableGenerateOptions'
import { SimpleCell } from '@vkontakte/vkui'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'

import { IndicatorTableOptionsSequence } from './IndicatorTableOptionsSequence'

import { MODAL_PAGES } from 'router/modals'

export const CellTableOptionsSequence: React.FC = () => {
  const router = useRouter()

  const isDisabledChangeDirectionSequence = useAppSelector(
    selectIsDisabledChangeDirectionSequence
  )

  const onClickTableSequence = () => {
    router.pushModal(MODAL_PAGES.TABLE_SEQUENCE)
  }

  return (
    <SimpleCell
      activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
      expandable
      disabled={isDisabledChangeDirectionSequence}
      // subtitle={isDisabledChangeDirectionSequence && 'Недоступно'}
      indicator={<IndicatorTableOptionsSequence />}
      onClick={onClickTableSequence}
    >
      Порядок символов
    </SimpleCell>
  )
}
