import React from 'react'
import { Icon24DoneOutline } from '@vkontakte/icons'
import {
  Card,
  CardGrid,
  Group,
  ModalPage,
  ModalPageHeader,
  ModalPageProps,
  PanelHeaderBack,
  Separator,
  SimpleCell,
} from '@vkontakte/vkui'
import { SeparatorSimpleCell } from 'components/ui/SeparatorSimpleCell/SeparatorSimpleCell'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { useModalClose } from 'hooks/modal'
import { isAddSeparator } from 'utils/list'

export const ModalPageTableOptionsStyleSelected: React.FC<ModalPageProps> = (
  modalPageProps
) => {
  const onClickBack = useModalClose()

  return (
    <ModalPage
      {...modalPageProps}
      header={
        <>
          <ModalPageHeader before={<PanelHeaderBack onClick={onClickBack} />}>
            Выбранные элементы
          </ModalPageHeader>
          <Separator wide />
        </>
      }
    >
      <Group>
        <CardGrid size='l'>
          <Card>
            {styles.map((style, idx) => (
              <React.Fragment key={style}>
                <SimpleCell
                  activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                  after={true && <Icon24DoneOutline />}
                >
                  {style}
                </SimpleCell>
                {isAddSeparator(styles, idx) && <SeparatorSimpleCell />}
              </React.Fragment>
            ))}
          </Card>
        </CardGrid>
      </Group>
    </ModalPage>
  )
}
const styles = ['none', 'translucent', 'hide_symbol', 'hide_cell']
