import { Page, RouteList } from '@happysanta/router'
import {
  PAGE_ABOUT,
  PAGE_HELP,
  PAGE_MAIN,
  PAGE_OPTIONS,
  PAGE_SETTINGS,
  PAGE_SETTINGS_APPEARANCE,
  PAGE_SETTINGS_NOTIFICATIONS,
  PAGE_TABLE,
  PAGE_SETTINGS_MODE_SEARCH,
  PAGE_STATISTICS,
} from './pages'

import { PANELS } from './panels'
import { ROOTS } from './roots'
import { VIEWS } from './views'

export const routes: RouteList = {
  [PAGE_MAIN]: new Page(PANELS.MAIN, VIEWS.MAIN, ROOTS.MAIN),

  [PAGE_HELP]: new Page(PANELS.HELP, VIEWS.OPTIONS, ROOTS.MAIN),
  [PAGE_ABOUT]: new Page(PANELS.ABOUT, VIEWS.OPTIONS, ROOTS.MAIN),
  [PAGE_OPTIONS]: new Page(PANELS.OPTIONS, VIEWS.OPTIONS, ROOTS.MAIN),
  [PAGE_SETTINGS]: new Page(PANELS.SETTINGS, VIEWS.OPTIONS, ROOTS.MAIN),

  [PAGE_SETTINGS_NOTIFICATIONS]: new Page(
    PANELS.SETTINGS_NOTIFICATIONS,
    VIEWS.OPTIONS,
    ROOTS.MAIN
  ),
  [PAGE_SETTINGS_APPEARANCE]: new Page(
    PANELS.SETTINGS_APPEARANCE,
    VIEWS.OPTIONS,
    ROOTS.MAIN
  ),
  [PAGE_SETTINGS_MODE_SEARCH]: new Page(
    PANELS.SETTINGS_MODE_SEARCH,
    VIEWS.OPTIONS,
    ROOTS.MAIN
  ),

  [PAGE_TABLE]: new Page(PANELS.TABLE, VIEWS.MAIN, ROOTS.MAIN),

  [PAGE_STATISTICS]: new Page(
    PANELS.STATISTICS,
    VIEWS.STATISTICS,
    ROOTS.STATISTICS
  ),
}
