import React from 'react'
import { useAppSelector } from 'hooks/redux'
import { selectChangeableTableVariant } from 'store/selectors/tableGenerateOptions'
import { IndicatorTitle } from 'components/ui/IndicatorTitle/IndicatorTitle'
import { TABLE_VARIANT } from 'data/table'

export const IndicatorTableOptionsVariant: React.FC = () => {
   const tableVariant = useAppSelector(selectChangeableTableVariant)
   return <IndicatorTitle>{TABLE_VARIANT[tableVariant]}</IndicatorTitle>
}
