import React from 'react'
import { useAppSelector } from 'hooks/redux'
import { selectChangeableTableSize } from 'store/selectors/tableGenerateOptions'
import { IndicatorTitle } from 'components/ui/IndicatorTitle/IndicatorTitle'
import { getSizeWithX } from 'store/slices/tableGenerateOptions'

export const IndicatorTableOptionsSize: React.FC = () => {
   const tableSize = useAppSelector(selectChangeableTableSize)
   return <IndicatorTitle>{getSizeWithX(tableSize)}</IndicatorTitle>
}
