import { createAsyncThunk } from '@reduxjs/toolkit'
import { getRandomString } from '@vkontakte/vkjs'
import { cloneDeep } from 'lodash'
import { RootState } from 'store'
import { IActiveTableState } from 'store/slices/activeTableSlice'
import { generateTable } from 'table/generator'
import { IActiveTableData, ICell } from 'types/table'
import { randomSort } from 'utils/array'
import { getRandomColor } from 'utils/color'
import { getRandomNumber } from 'utils/number'

const flips = ['x', 'y', 'both']

export const setTable = createAsyncThunk<IActiveTableState, undefined>('activeTable/set', async (_, thunkAPI) => {
   try {
      const tableGenerateOptions = (thunkAPI.getState() as RootState).tableGenerateOptions.currentTableOptions
      const tableOptions = (thunkAPI.getState() as RootState).tableOptions

      const { s: sequenceCells, d: displayedCells, sequence } = generateTable(tableGenerateOptions)

      const table: IActiveTableData = {
         id: Date.now().toString(),
         sequence,
         sequenceCells,
         displayedCells,

         currentSequenceCell: cloneDeep(sequenceCells[0]),
         idxOfCurrentCellInSequence: 0,
         foundCells: [],
         wrongFoundCells: [],
         startedAt: null,
         completedAt: null,
         status: null,
      }
      const activeTable: IActiveTableState = {
         data: table,
         options: tableOptions,
         generateOptions: tableGenerateOptions,
      }
      return activeTable
   } catch (e) {
      return thunkAPI.rejectWithValue(e)
   }
})
