import React from 'react'
import {
  ModalPage,
  ModalPageHeader,
  ModalPageProps,
  PanelHeaderBack,
  Separator,
} from '@vkontakte/vkui'
import { useRouter } from '@happysanta/router'
import { GroupTableVariant } from 'components/layout/modals/pages/tableOptions/variant/groups/GroupTableVariant'
import { useModalClose } from 'hooks/modal'

export const ModalPageTableParamsVariant: React.FC<ModalPageProps> = (
  modalPageProps
) => {
  const router = useRouter()

  const onClickBack = useModalClose() //() => router.popPageIfModal()

  return (
    <ModalPage
      {...modalPageProps}
      header={
        <>
          <ModalPageHeader before={<PanelHeaderBack onClick={onClickBack} />}>
            Вариант
          </ModalPageHeader>
          <Separator wide />
        </>
      }
    >
      <GroupTableVariant />
    </ModalPage>
  )
}
