import { RootState } from 'store'

export const selectConfigIsOnline = (state: RootState) => state.config.isOnline
export const selectConfigIsActiveSound = (state: RootState) => state.config.sound
export const selectConfigIsActiveVibration = (state: RootState) => state.config.vibration

export const selectConfigAppearanceType = (state: RootState) => state.config.appearance.type
export const selectConfigAppearanceValue = (state: RootState) => state.config.appearance.value
