import React from 'react'
import {
  ModalPage,
  ModalPageHeader,
  ModalPageProps,
  PanelHeaderBack,
  Separator,
} from '@vkontakte/vkui'
import { GroupTableOptionsSequence } from './groups/GroupTableOptionsSequence'
import { useModalClose } from 'hooks/modal'

export const ModalPageTableOptionsSequence: React.FC<ModalPageProps> = (
  modalPageProps
) => {
  const onClickBack = useModalClose()

  return (
    <ModalPage
      {...modalPageProps}
      header={
        <>
          <ModalPageHeader before={<PanelHeaderBack onClick={onClickBack} />}>
            Порядок
          </ModalPageHeader>
          <Separator wide />
        </>
      }
    >
      <GroupTableOptionsSequence />
    </ModalPage>
  )
}
