import React from 'react'
import { Icon28DoneOutline } from '@vkontakte/icons'
import { Group, SimpleCell } from '@vkontakte/vkui'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { selectChangeableTableVariant } from 'store/selectors/tableGenerateOptions'
import { setMainTableOptionsVariant } from 'store/slices/tableGenerateOptions'

import { GroupCard } from 'components/ui/GroupCard/GroupCard'
import { dataTableVariants, TABLE_VARIANT } from 'data/table'
import { TypeTableVariant } from 'types/table'

export const GroupTableVariant: React.FC = () => {
   const dispatch = useAppDispatch()

   const tableVariant = useAppSelector(selectChangeableTableVariant)

   const onClickTableVariant = (_tableVariant: TypeTableVariant) => () => {
      dispatch(setMainTableOptionsVariant(_tableVariant))
   }
   return (
      <Group>
         {dataTableVariants.map((tableVariantItem) => (
            <GroupCard key={tableVariantItem}>
               <SimpleCell
                  activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                  after={tableVariant === tableVariantItem && <Icon28DoneOutline />}
                  onClick={onClickTableVariant(tableVariantItem)}
               >
                  {TABLE_VARIANT[tableVariantItem]}
               </SimpleCell>
            </GroupCard>
         ))}
      </Group>
   )
}
