import React from 'react'
import { SimpleCell } from '@vkontakte/vkui'

import { SwitchTableTrackingMouse } from './switchies/SwitchTableTrakingMouse'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'

export const CellTableTrackingMouse: React.FC = () => {
  return (
    <SimpleCell
      disabled
      activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
      after={<SwitchTableTrackingMouse />}
    >
      Отслеживание мыши
    </SimpleCell>
  )
}
