import React from 'react'
import {
  FixedLayout,
  PanelHeader,
  PanelHeaderProps,
  Separator,
  useAdaptivityWithJSMediaQueries,
} from '@vkontakte/vkui'

interface PanelHeaderSeparatorProps extends PanelHeaderProps {
  isSeparator?: boolean
}

export const PanelHeaderSeparator: React.FC<PanelHeaderSeparatorProps> = ({
  isSeparator = true,
  ...panelHeaderProps
}) => {
  const { isDesktop } = useAdaptivityWithJSMediaQueries()

  return (
    <>
      <PanelHeader
        separator={isDesktop}
        {...panelHeaderProps}
      />
      {!isDesktop && isSeparator && (
        <FixedLayout vertical='top'>
          <Separator wide />
        </FixedLayout>
      )}
    </>
  )
}
