import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { VKWebAppTapticImpactOccurred } from 'API/bridge'
import {
  IActiveTableData,
  ICell,
  IFoundCell,
  ICurrentSequenceCell,
  ITableOptions,
  // TypeTableSymbolsSearchMode,
  IWrongFoundCell,
  ITableGenerateOptions,
} from 'types/table'
import { randomSort } from 'utils/array'
import { getRandomColor } from 'utils/color'

import cloneDeep from 'lodash/cloneDeep'
import { defaultTableGenerateOptions } from './tableGenerateOptions'
import { shuffleArray } from 'table/generator'
import { getRandomNumber } from 'utils/number'
import { setTable } from 'store/asyncThunks/activeTable'
import { defaultTable } from 'table/data'
import { defaultTableOptions } from './tableOptions'

export interface IActiveTableState {
  data: IActiveTableData
  options: ITableOptions
  generateOptions: ITableGenerateOptions
}

const initialState: IActiveTableState = {
  data: defaultTable,
  generateOptions: cloneDeep(defaultTableGenerateOptions),
  options: cloneDeep(defaultTableOptions),
}

const getDataCurrentSequenceCell = (
  state: IActiveTableState
): ICurrentSequenceCell => {
  const cell = state.data.sequenceCells[state.data.idxOfCurrentCellInSequence]
  return {
    sequenceValue: cell.sequenceValue,
    visibleValue: cell.visibleValue,
    backgroundColorMode: cell.backgroundColorMode,
    symbolColorMode: cell.symbolColorMode,
  }
}

const shuffleCells = (state: IActiveTableState) => {
  state.data.displayedCells.sort(randomSort)
}

const updateColors = (state: IActiveTableState) => {
  state.data.displayedCells.forEach((cell) => {
    if (cell.backgroundColorMode === 'custom' && cell.backgroundColor) {
      cell.backgroundColor = getRandomColor()
    }

    if (cell.symbolColorMode === 'custom' && cell.symbolColor) {
      cell.symbolColor = getRandomColor()
    }
  })
}

const activeTableSlice = createSlice({
  name: 'activeTable',
  initialState,
  reducers: {
    startSolution: (state) => {
      state.data.startedAt = Date.now()
      state.data.status = 'started'
    },

    /**
     * @param action.payload `number` - idx в массиве `displayedCells`
     * @param action.payload `undefined` - взять текущий символ из `sequenceCells`
     */
    clickDisplayedCell: (state, action: PayloadAction<number | undefined>) => {
      const current =
        state.data.sequenceCells[state.data.idxOfCurrentCellInSequence]

      if (!current) return

      const cell =
        typeof action.payload === 'number'
          ? state.data.displayedCells[action.payload]
          : state.data.displayedCells.find(
              (c) => c.sequenceValue === current.sequenceValue
            )

      if (!cell) return

      if (current.sequenceValue === cell.sequenceValue) {
        VKWebAppTapticImpactOccurred()

        state.data.idxOfCurrentCellInSequence++

        const isMarkedSelected = true

        if (isMarkedSelected) {
          cell.visibility = 'translucent'
        }

        const foundCell: IFoundCell = {
          cell: { ...cell },

          ts: Date.now(),
        }

        state.data.foundCells.push(foundCell)

        if (
          state.data.idxOfCurrentCellInSequence <=
          state.data.sequenceCells.length - 1
        ) {
          if (state.options.isShuffleCellsAfterSelect) {
            shuffleCells(state)
          }

          if (state.generateOptions.isColoredSymbols) {
            updateColors(state)
          }

          state.data.currentSequenceCell = getDataCurrentSequenceCell(state)
        } else {
          // state.data.currentSequenceCell = null
          state.data.completedAt = Date.now()
          state.data.status = 'compleated'
        }

        // console.log('progress: ', state.data.idxOfCurrentCellInSequence / state.data.sequenceCells.length)
      } else {
        const wrongFoundCell: IWrongFoundCell = {
          cell: { ...cell },
          currentCell: { ...current },
          // searchMode,
          ts: Date.now(),
        }
        state.data.wrongFoundCells.push(wrongFoundCell)
      }

      /*    const stats = state.data.clickedCells.map((click, idx, arr) => {
            const v = { time: 0, value: '', action: '' as TypeClickedCellAction }

            if (idx === 0) {
               v.time = click.ts - state.data.startedAt!
            } else {
               v.time = click.ts - arr[idx - 1].ts
            }
            v.value = click.cell.symbol.value
            v.action = click.action
            return v
         }) */

      // console.log(stats)
    },

    /*   clickFinded: (state) => {
         if (state.data.status === 'compleated' || state.data.status === 'closed') return

         const current = state.data.sequenceCells[state.data.idxOfCurrentCellInSequence]

         if (!current) return
         const cell = state.data.displayedCells.find((c) => c.id === current.id)
         if (!cell) return

         VKWebAppTapticImpactOccurred()
         state.data.idxOfCurrentCellInSequence++

         if (state.data.idxOfCurrentCellInSequence <= state.data.sequenceCells.length - 1) {
            if (state.options.isShuffleCellsAfterSelect) {
               shuffleCells(state)
            }

            if (state.options.isUpdateColorsAfterSelect) {
               updateColors(state)
            }

            state.data.currentSequenceCell = getDataCurrentSequenceCell(state)
         } else {
            state.data.currentSequenceCell = null
            state.data.completedAt = Date.now()
            state.data.status = 'compleated'
         }
      }, */

    autoChangeColor: (state) => {
      const idx = getRandomNumber(0, state.data.displayedCells.length - 1)
      const cell = state.data.displayedCells[idx]

      if (!cell) return

      // if (cell?.background?.backgroundColorMode === 'custom' && !!cell?.background?.backgroundColor) {
      //    cell.background.backgroundColor = getRandomColor()
      // }

      // if (cell.symbol?.colorMode === 'custom' && !!cell.symbol?.color) {
      //    cell.symbol.color = getRandomColor()
      // }

      // if (cell.id === state.data.currentSequenceCell?.id) {
      //    if (state.data.currentSequenceCell.background?.backgroundColor) {
      //       state.data.currentSequenceCell.background.backgroundColor = cell?.background?.backgroundColor
      //    }
      //    if (state.data.currentSequenceCell.symbol?.color) {
      //       state.data.currentSequenceCell.symbol.color = cell.symbol?.color
      //    }
      // }
    },
  },
  extraReducers: (builder) => {
    builder.addCase(setTable.pending, (state, action) => {})
    builder.addCase(setTable.fulfilled, (state, action) => {
      state.data = action.payload.data
      state.options = action.payload.options
      state.generateOptions = action.payload.generateOptions
    })
    builder.addCase(setTable.rejected, (state, action) => {})
  },
})
export const { startSolution, clickDisplayedCell, autoChangeColor } =
  activeTableSlice.actions
export const activeTableReducer = activeTableSlice.reducer
