import { createAsyncThunk } from '@reduxjs/toolkit'
import { IConfigAppearance } from 'store/slices/configSlice'
import { RootState } from 'store'

import { VKWebAppSetViewSettings } from 'API/bridge'
import { ConfigAppearanceType } from 'data/config'

export const setAppearance = createAsyncThunk<IConfigAppearance, ConfigAppearanceType>(
   'config/setAppearance',
   async (type: ConfigAppearanceType, thunkAPI) => {
      try {
         const configAppearance: IConfigAppearance = {
            type,
            value: 'light',
         }
         if (type === 'auto') {
            const configAppearanceValue = (thunkAPI.getState() as RootState).config.clientConfig.appearance
            configAppearance.value = configAppearanceValue
         } else {
            configAppearance.value = type
         }
         VKWebAppSetViewSettings(configAppearance.value)

         return configAppearance
      } catch (e) {
         return thunkAPI.rejectWithValue(e)
      }
   }
)

export const getPromoBannerData = createAsyncThunk('config/getPromoBannerData', async (_, thunkAPI) => {
   try {
      const data = ''
      return data
   } catch (e) {
      return thunkAPI.rejectWithValue(e)
   }
})
