import React from 'react'

import { classNames } from '@vkontakte/vkui'

import './TableContainer.css'

interface TableContainerProps {
   outline?: boolean
   rounded?: boolean
}
export const TableContainer: React.FC<TableContainerProps> = ({ outline, rounded, children }) => {
   return (
      <div
         className={classNames(
            'TableContainer',
            outline && 'TableContainer--outline',
            rounded && 'TableContainer--rounded'
         )}
      >
         <div className='TableContainer__in'>
            <div className='TableContainer__children'>{children}</div>
         </div>
      </div>
   )
}
