import React from 'react'
import { useLocation } from '@happysanta/router'
import { View } from '@vkontakte/vkui'
import { PanelMain } from '../panels/main/PanelMain'
import { NavProp } from 'types/nav'
import { PANELS } from 'router/panels'
import { PanelTable } from '../panels/main/PanelTable'
import { useAppDispatch } from 'hooks/redux'
import { startSolution } from 'store/slices/activeTableSlice'

export interface ITransitionView {
   isBack: boolean
   from: string
   to: string
}
export type TypeTansitionViewHandler = (params: ITransitionView) => void

export const ViewMain: React.FC<NavProp> = ({ nav }) => {
   const dispatch = useAppDispatch()
   const location = useLocation()
   const activePanel = location.getViewActivePanel(nav) || PANELS.MAIN

   const onTransitionView: TypeTansitionViewHandler = ({ to }) => {
      if (to === PANELS.TABLE) {
         dispatch(startSolution())
      }
      // console.log({ from, to })
   }
   return (
      <View activePanel={activePanel} onTransition={onTransitionView}>
         <PanelMain nav={PANELS.MAIN} />
         <PanelTable nav={PANELS.TABLE} />
      </View>
   )
}
