import React from 'react'
import { useRouter } from '@happysanta/router'
import { Button } from '@vkontakte/vkui'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { useAppDispatch } from 'hooks/redux'
import { MODAL_PAGES } from 'router/modals'
import { PAGE_TABLE } from 'router/pages'

import { setTable } from 'store/asyncThunks/activeTable'
import { POPUPS } from 'router/popups'
import { useSetAnchorElement } from 'components/layout/popouts/PopoutContext'

export const ButtonTableStart: React.FC = () => {
  const buttonRef = React.useRef<HTMLDivElement>(null)
  const setAnchorElement = useSetAnchorElement()
  const router = useRouter()
  const dispatch = useAppDispatch()

  const onClickTableStart = () => {
    setAnchorElement(buttonRef.current)
    router.pushPopup(POPUPS.ACTION_SHEET_TRAINING_MODE)
    // dispatch(setTable()).then(() => router.pushPage(PAGE_TABLE))
  }
  return (
    <Button
      getRootRef={buttonRef}
      onClick={onClickTableStart}
      stretched
      activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
      // Button--round
      className='Button--stretched-content Tappable--active-opacity Button--round'
      size='l'
      // mode='tertiary'
      mode='primary'
      // appearance='neutral'
      appearance='accent'
    >
      Начать
    </Button>
  )
}
export const ButtonTableOptions: React.FC = () => {
  const router = useRouter()
  // const dispatch = useAppDispatch()

  const onClickTableOptions = () => {
    // dispatch(startTable()).then(() => router.pushPage(PAGE_TABLE))
    router.pushModal(MODAL_PAGES.TABLE_OPTIONS)
  }

  return (
    <Button
      onClick={onClickTableOptions}
      stretched
      activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
      // Button--round
      className='Button--stretched-content Tappable--active-opacity Button--round'
      size='l'
      // mode='tertiary'
      mode='outline'
      // appearance='neutral'
      appearance='accent'
    >
      Параметры
    </Button>
  )
}
