import React from 'react'
import { classNames } from '@vkontakte/vkui'

import './TableWrapper.css'

interface TableWrapperProps extends React.HTMLAttributes<HTMLDivElement> {
   table?: React.ReactElement
   top?: React.ReactElement
   bottom?: React.ReactElement
}

export const TableWrapper: React.FC<TableWrapperProps> = ({ top, bottom, table, ...restProps }) => {
   return (
      <div {...restProps} className={classNames('TableWrapper')}>
         <div className='TableWrapper__top'>{top}</div>
         <div className='TableWrapper__table'>{table}</div>
         <div className='TableWrapper__bottom'>{bottom}</div>
         {/* <TableContainer rounded outline>
               <ButtonStart />
            </TableContainer> */}
      </div>
   )
}

