import React from 'react'
import { List, Separator, SimpleCell } from '@vkontakte/vkui'

import { isAddSeparator } from 'utils/list'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { Icon28DoneOutline } from '@vkontakte/icons'

import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { setAppearance } from 'store/asyncThunks/config'

import { ConfigAppearanceType, CONFIG_APPEARANCE, dataConfigAppearances } from 'data/config'
import { selectConfigAppearanceType } from 'store/selectors/config'

// TODO Причесать лист. Не нравится
export const ListAppearanceItems: React.FC = () => {
   const dispatch = useAppDispatch()

   const appearanceType = useAppSelector(selectConfigAppearanceType)

   const onClickAppearance = (_appearance: ConfigAppearanceType) => () => {
      dispatch(setAppearance(_appearance))
   }

   return (
      <List>
         {dataConfigAppearances.map((appearanceItem, idx, items) => (
            <React.Fragment key={appearanceItem}>
               <SimpleCell
                  activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                  after={appearanceType === appearanceItem && <Icon28DoneOutline />}
                  onClick={onClickAppearance(appearanceItem)}
               >
                  {CONFIG_APPEARANCE[appearanceItem]}
               </SimpleCell>
               {isAddSeparator(items, idx) && <Separator wide />}
            </React.Fragment>
         ))}
      </List>
   )
}
