import { createSlice } from '@reduxjs/toolkit'
import { ITable } from 'types/table'

export type TypeModeTraining = 'table' | 'timer' | 'mistake'

interface IActiveTrainingState {
  mode: TypeModeTraining
  timer: number

  table: undefined //IActiveTable
  timePassed: number

  completedTables: ITable[]
  // completedTables: number

  tsStart: number
  tsСompletion: number | null
  statusCompletion: 'done' | 'close' | 'mistake' | null
}

const initialState: IActiveTrainingState = {
  mode: 'table',
  timer: 3,
  table: undefined,

  completedTables: [],

  timePassed: 0,
  tsStart: Date.now(),
  tsСompletion: null,
  statusCompletion: null,
}

const activeTrainingSlice = createSlice({
  name: 'activeTraining',
  initialState,
  reducers: {
    startTraining: (state, action) => {},
  },
  extraReducers: (builder) => {},
})

export const activeTrainingReducer = activeTrainingSlice.reducer

export const {} = activeTrainingSlice.actions
