import React from 'react'

import { CellTableOptionsMainVariant } from './cells/variant/CellTableOptionsMainVariant'
import { GroupCard } from 'components/ui/GroupCard/GroupCard'
import { CellTableOptionsSequence } from './cells/sequence/CellTableOptionsSequence'
import { CellTableOptionsIsShuffleCells } from './cells/shuffle/CellTableOptionsIsShuffleCells'
import { CellTableOptionsSize } from './cells/size/CellTableOptionsSize'

import { CellTableOptionsTransforms } from './cells/transforms/CellTableOptionsTransforms'
import { CellTableOptionsColor } from './cells/color/CellTableOptionsColor'
import { SeparatorSimpleCell } from 'components/ui/SeparatorSimpleCell/SeparatorSimpleCell'
import { CellTableOptionsStyleSelected } from './cells/selected/CellTableOptionsStyleSelected'
import { Card, CardGrid, Group, SimpleCell } from '@vkontakte/vkui'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { IndicatorTitle } from 'components/ui/IndicatorTitle/IndicatorTitle'
import { useRouter } from '@happysanta/router'
import { MODAL_PAGES } from 'router/modals'

export const GroupTableOptionsMain: React.FC = () => {
  return (
    <>
      {/* <Group
        mode='plain'
        separator='hide'
      >
        <CardGrid size='l'>
          <Card>
            <CellTrainingMode />
          </Card>
        </CardGrid>
      </Group> */}
      <GroupCard>
        <CellTableOptionsSize />
        <SeparatorSimpleCell />
        <CellTableOptionsMainVariant />
        <SeparatorSimpleCell />
        <CellTableOptionsSequence />
        <SeparatorSimpleCell />
        <CellTableOptionsTransforms />
        <SeparatorSimpleCell />
        <CellTableOptionsColor />
        <SeparatorSimpleCell />
        <CellTableOptionsStyleSelected />
        <SeparatorSimpleCell />

        <CellTableOptionsIsShuffleCells />
      </GroupCard>
    </>
  )
}

const CellTrainingMode: React.FC = () => {
  const router = useRouter()
  const onClickTrainingMode = () => {
    router.pushModal(MODAL_PAGES.TRAINING_MODE)
  }
  return (
    <SimpleCell
      activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
      expandable
      indicator={<IndicatorTitle>iii</IndicatorTitle>}
      onClick={onClickTrainingMode}
    >
      Режим
    </SimpleCell>
  )
}
